import React from 'react'
import { Query, Mutation } from 'react-apollo'
import gql from 'graphql-tag'
import styled from 'styled-components'

const OPEN_MODAL_QUERY = gql`
  query {
    openModal @client
  }
`
const TOGGLE_MODAL_MUTATION = gql`
  mutation {
    toggleModal @client
  }
`
const BackDrop = styled.div`
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
`

const Modall = styled.div`
  background-color: #fff;
  border-radius: 25px;
  flex-flow: column;
  display: grid;
  grid-template-rows: 50px 1fr 40px;
  position: absolute;
  align-items: center;
  width: 500px;
  min-height: 300px;

  z-index: 900;
`

const Cancel = styled.button`
  background-color: #fff;
  border-radius: 5px;
  display: grid;
  grid-row: 3;
  position: relative;
  align-items: center;
  width: 50px;
  height: 100%;
  background: rgba(20, 100, 220, 1);
  z-index: 900;
`
const Save = styled.button`
  background-color: #fff;
  border-radius: 5px;

  display: grid;
  grid-row: 3;
  position: relative;
  align-items: center;
  width: 50px;
  height: 100%;

  z-index: 900;
`
class Modal extends React.Component {
  render() {
    return (
      <Mutation mutation={TOGGLE_MODAL_MUTATION}>
        {toggleModal => (
          <Query query={OPEN_MODAL_QUERY}>
            {({ data }) => {
              if (!data.openModal) {
                return null
              }
              return (
                <BackDrop>
                  <Modall>
                    <p>Hey</p>
                    <div />
                    <Cancel onClick={toggleModal}>Cancel</Cancel>
                    <Save onClick={toggleModal}>Save</Save>
                  </Modall>
                </BackDrop>
              )
            }}
          </Query>
        )}
      </Mutation>
    )
  }
}

export default Modal
export { OPEN_MODAL_QUERY, TOGGLE_MODAL_MUTATION }
